import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/view/HelloWorld'
import Detail from '@/view/Detail'
import Chat from '@/view/chatRoom.vue'
import Login from '@/view/login.vue'
Vue.use(Router)


// travis 
var router = new Router({
  mode: 'history',
  base: '/yirenzhixia/',
  routes: [
    { 
      path: "/", 
      name: "login", 
      component: Login 
    },
    {
      path: '/diretory',
      name: 'HelloWorld',
      component: HelloWorld,
      meta: { requiresAuth: true }
    },
    {
      path: '/detail/:chapterId',
      name: 'detail',
      component: Detail,
      // props true   propss
      props: true,
      meta: { requiresAuth: true }
    },
    {
      path: "/chat",
      name: 'chat',
      component: Chat,
      meta: { requiresAuth: true }
    }
  ],
  scrollBehavior(to, from, savedPosition) {
    console.log("route", savedPosition)
    //   
    if (savedPosition) return savedPosition;
    // ()  
    else if (to.hash && document.querySelector(to.hash)) return { selector: to.hash }
    //     
    return { x: 0, y: 0 }
  },
  
})
router.beforeEach = function(from, to, next){
  if(to.meta.requiresAuth && !this.$store.state.chat.isLoggin){
    next("/")
  }
  next()
}
export default router



